.header {
  height: 55px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: white;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: rgb(226, 223, 223);
  z-index: 100;
}

.left-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.hamburger-menu {
  height: 24px;
  margin-left: 24px;
  margin-right: 20px;
}

.youtube-logo {
  height: 20px;
}

.middle-section {
  flex: 1;
  margin-left: 70px;
  margin-right: 35px;
  max-width: 500px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.search-bar {
  flex: 1;
  width: 0;
  height: 36px;
  padding-left: 10px;
  font-size: 16px;
  border-width: 1px;
  border-style: solid;
  border-color: rgb(192, 192, 192);
  border-radius: 2px;
  box-shadow: inset 1px 2px 3px rgba(228, 228, 228, 0.05);
}

.search-bar::placeholder {
  font-size: 14px;
}

.search-button {
  height: 39px;
  width: 66px;
  background-color: rgb(238, 238, 238);
  border-width: 1px;
  border-style: solid;
  border-color: rgb(192, 192, 192);
  margin-left: -1px;
  margin-right: 10px;
  position: relative;
}

.search-button .tooltip,
.voice-search-button .tooltip,
.upload-icon-container .tooltip,
.youTube-icon-container .tooltip,
.notification-container .tooltip {
  position: absolute;
  cursor: pointer;
  background-color: grey;
  color: white;
  padding-top: 4px;
  padding-left: 8px;
  padding-bottom: 4px;
  padding-right: 8px;
  border-radius: 2px;
  font-size: 12px;
  bottom: -30px;
  opacity: 0;
  transition: opacity 0.15s;
  pointer-events: none;
  white-space: nowrap;
}

.search-icon {
  height: 25px;
}

.search-button:hover .tooltip,
.voice-search-button:hover .tooltip,
.upload-icon-container:hover .tooltip,
.youTube-icon-container:hover .tooltip,
.notification-container:hover .tooltip {
  opacity: 1;
}

.search-button,
.voice-search-button,
.upload-icon-container,
.youTube-icon-container,
.notification-container {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.voice-search-button {
  height: 40px;
  width: 40px;
  border-radius: 20px;
  border: none;
  background-color: rgb(244, 244, 244);
  position: relative;
}

.voice-search-icon {
  height: 25px;
}

.right-section {
  width: 180px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 20px;
  flex-shrink: 0;
}

.upload-icon {
  height: 24px;
}

.youtube-icon {
  height: 24px;
}

.notification-icon {
  height: 24px;
}

.current-user-icon {
  height: 32px;
  border-radius: 16px;
}
